import { undividedOwnershipType } from "../../../../../ChatbotComponent/Helper/Enums/ChatStepTypes";
import { InheritanceCalculation } from "../../../../InheritanceCalculation";
import { currencyFormatNO } from "../pdf_utils";

const undivided_estate_results = (value: InheritanceCalculation) => {
  const finalresult = "";
  const first_partly_separate = value.state.undividedEstate.temp_first;
  const testator_name = value.actionProvider.getPerson(
    value.state.testator._id,
    value.state.personsMap
  )._personName;
  const second_partly_separate = value.state.undividedEstate.temp_last;
  const undivided_spouse_name = value.actionProvider.getPerson(
    value.state.undividedSpouseId,
    value.state.personsMap
  )._personName;
  const testator_wealth = currencyFormatNO(value.state.netWealth);
  const undivided_estate_separate_wealth = currencyFormatNO(
    value.state.undividedEstate.undividedEstateSeparateWealth
  );
  if (value.state.undividedEstate.undividedEstateChoice) {
    finalresult.concat("<Text>Arvelater satt i uskiftet bo. </Text");
    if (
      value.state.undividedEstate.ownershipType ===
      undividedOwnershipType.felleseie
    ) {
      finalresult.concat(
        `<Text>Det uskiftede boet var utelukkende felleseie. Boet verdier fordeles likt p frstavddes og sistavddes arvinger, ${value.state.undividedEstate.undividedEstateSeparateWealth} p hver gruppe.</Text>`
      );
    } else if (
      value.state.undividedEstate.ownershipType ===
      undividedOwnershipType.delvis
    ) {
      finalresult.concat(
        `<Text>Det uskiftede boet var delvis sreie. Frstavddes (${undivided_spouse_name}) sreie hadde en verdi p ${first_partly_separate}, mens sistavdedes (${testator_name}) sreie hadde en verdi p ${second_partly_separate}. Verdiene som fordeles blant frstavddes (${undivided_spouse_name}) arvinger er ${testator_wealth}, mens verdiene som fordeles blant sistavddes (${testator_name})  arvinger er ${undivided_estate_separate_wealth}.</Text>`
      );
    } else if (
      value.state.undividedEstate.ownershipType === undividedOwnershipType.fullt
    ) {
      finalresult.concat(
        `<Text>Det uskiftede boet var fullt sreie. Verdiene som fordeles blant frstavddes (${undivided_spouse_name})  arvinger er ${testator_wealth}, mens verdiene som fordeles blant sistavddes (${testator_name}) arvinger er ${undivided_estate_separate_wealth}.</Text>`
      );
    } else {
      console.log("check for problem, untroduction.utils.ts");
    }
  }
  return finalresult;
};

export const IntroductionUtils = (value: InheritanceCalculation) => {
  const currencyFormatted = currencyFormatNO(value.state.netWealth);

  const testatorName = value.actionProvider.getPerson(
    1,
    value.state.personsMap
  )._personName;
  return {
    currencyFormatted: currencyFormatted,
    testatorName: testatorName,
  };
};
